/* Time Input - Time Picker */

function initTimepicker() {
  $('[data-timepicker-trigger]').each(function(){
    var toggleInput = $(this).find('input');

    toggleInput.timepicker({
      appendWidgetTo: $(this)
    });
  });
}


// For mobile browsers, change the time picker inputs to type time in order to force the native UI on the phone/tablet
if (/Mobi/.test(navigator.userAgent)) {
  $('[data-timepicker-trigger] input').attr('type', 'time');
  $('[data-timepicker-trigger] button').on('click', function(e){
    $(e.target).siblings('input').get(0).focus();
  });
} else {

  initTimepicker();
}

