/* Process List */

// Show only 1 label when screen is too small to fit multiple labels
function initProcessList() {
    $(window).on("resize", function () {
        $( ".vaux-process-list.has-bottom-label" ).each(function( ) {
            // capture 2 key widths in variables
            var widthContainer = $(this).width();
            var widthLabels = 0;

            // capture the bottom label container
            var bottomLabel = $('.process-bottom-label');

            // capture the Current step's text
            var currentStepText = $(this).find('ol .current span').text();

            // recalculate width of all the labels together
            $(this).find('span').each(function() {
                widthLabels += parseInt($(this).width(), 10);
            });

            // reset the bottom label
            $(this).find(bottomLabel).empty();

            // remove the class
            $(this).removeClass('has-bottom-label-activated');

            // If the container is too small to hold all Labels,
            // then show only the Bottom Label
            if (widthContainer <= widthLabels) {
                $(this).addClass('has-bottom-label-activated');

                // and add the Current step's text into it
                $(this).find(bottomLabel).append(currentStepText);
            }
        });
        // invoke this resize event immediately (on load)
    }).resize();
}

initProcessList();